body {
  margin: 0;
  background-color:#F9FAFB;
  font-family: 'Roboto', sans-serif;
}



@keyframes fadeInFromNone {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fadein {
  animation: linear 0.5s normal fadeInFromNone;
}




@-webkit-keyframes beat {
  0% {
      -webkit-transform: scale(1);
      transform: scale(1);
  }
  100% {
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
  }
}

@keyframes beat {
  0% {
      -webkit-transform: scale(1);
      transform: scale(1);
  }
  100% {
      -webkit-transform: scale(1.2);
      transform: scale(1.2);
  }
}


.beat {
  animation: beat linear .3s alternate  infinite;
  -webkit-animation: beat linear .3s alternate infinite;
}
